export default {
  name:'hl_fish_fire_config',
  data() {
    return {
      list: [],
      id: 0,
      keys: "",
      loading: false
    };
  },
  async created() {
    //获取数据
    let { data } = await this.$http.HallFunConfig.Getfishing_guns({
      key: "fishing_guns.lua"
    });
    // console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.list = res;
  },
  methods: {
    //添加
    add() {
      this.list.push({});
    },

    //保存和服务器配置
    async submit(type) {
      if (type === 1) {
        let { data } = await this.$http.HallFunConfig.Putfishing_guns({
          keys: this.keys,
          values: JSON.stringify(this.list),
          id: this.id
        });
        // console.log(data);
        if (data.code === 1 && data.msg === "ok") {
          this.$message({
            type: "success",
            message: "保存成功!"
          });
        } else {
          this.$message({
            type: "warning",
            message: "保存失败!"
          });
        }
      } else if (type === 2) {
        this.loading = true;
        // console.log(this.list);

        let { data } = await this.$http.HallFunConfig.Postfishing_guns({
          keys: this.keys,
          values: JSON.stringify(this.list),
          id: this.id
        });
        // console.log(data);
        if (data.code === 1 && data.msg === "ok") {
          this.loading = false;
          this.$message({
            type: "success",
            message: "发送到服务器配置成功!"
          });
        } else {
          this.loading = false;
          this.$message({
            type: "warning",
            message: "发送服务器配置失败!"
          });
        }
      }
    },

    //删除
    del(index) {
      this.$confirm("确定删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.list.splice(index, 1);
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};



